@import "includes/fonts";
@import "../../node_modules/bootstrap/scss/bootstrap-reboot.scss";

* {
    box-sizing: border-box; }

body {
    font-family: MuseoSansCyrl, sans-serif;
    font-weight: 500;
    font-size: 16px;
    color: #282828; }

ul {
    padding: 0;
    margin: 0;
    list-style: none; }

p, h1, h2, h3, h4, h5, h6 {
    margin: 0; }

a {
    color: #282828;
    &:hover {
        text-decoration: none;
        color: #282828; }
    &:focus, &:active {
        outline: 0; } }
img {
    &:hover, &:active, &:focus {
        outline: 0; } }

button {
    &:focus, &:active {
        outline: 0; } }

.container {
    max-width: 100%;
    width: 1200px;
    margin: 0 auto;
    padding: 0 15px; }

.header {
    background: url('../img/header/background.png');
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    overflow: hidden; }

.bottom-snow {
    position: absolute;
    width: 100%;
    height: auto;
    max-height: 510px;
    bottom: 0;
    left: 0;
    pointer-events: none;
    z-index: 5; }

.fallen-snow {
    position: absolute;
    left: 50%;
    top: 0;
    width: auto;
    min-width: 100%;
    height: 100%;
    pointer-events: none;
    transform: translateX(-50%); }

.menu-container {
    display: grid;
    grid-template: 1fr / 1fr 1fr;
    padding: 30px 0;
    font-weight: 300;
    font-size: 14px;
    line-height: 140%; }

.logo-part,
.contacts-part {
    display: flex;
    align-items: center; }

.contacts-part {
    justify-content: flex-end; }

.logo-desc {
    width: 195px;
    margin-left: 60px; }

.logo {
    width: 190px;
    height: auto; }

.logo-desc {
    color: #A7A7A7; }

.full-time {
    color: #282828;
    margin-bottom: 2px; }

.phone-link {
    font-weight: 700;
    font-size: 22px; }

.menu-button {
    margin-left: 30px;
    border-radius: 7px;
    border: 1px solid #84CD3A;
    background: none;
    line-height: 0;
    min-width: 200px;
    padding: 29px 20px;
    color: #84CD3A;
    text-transform: uppercase; }

.menu {
    margin-bottom: 70px;
    position: relative;
    z-index: 2; }

.header-txt-container {
    display: grid;
    grid-template: auto / 2fr 1fr; }

.header-txt-wrap {
    position: relative;
    z-index: 2; }

.header-title {
    font-weight: 500;
    font-size: 48px;
    line-height: 140%;
    color: #282828;
    margin-bottom: 25px; }

.header-title-accent {
    color: #84CD3A;
    font-weight: 700; }

.header-title-bordered {
    color: #84CD3A;
    font-size: 20px;
    width: 177px;
    line-height: 0;
    padding: 24px 0;
    display: inline-block;
    position: relative;
    top: -5px;
    margin-left: 20px;
    text-align: center;
    background: url('../img/gifts/background.svg');
    background-repeat: no-repeat;
    background-size: contain; }

.header-post-title {
    padding-left: 60px;
    position: relative;
    font-weight: 500;
    font-size: 22px;
    line-height: 140%;
    margin-bottom: 25px;
    span {
        font-weight: 700; }
    &:before {
        content: '';
        position: absolute;
        width: 35px;
        height: 2px;
        background: #84CD3A;
        left: 0;
        top: 50%;
        transform: translateY(-50%); } }

.gifts {
    display: grid;
    grid-template: auto / 1fr 1fr;
    max-width: 470px;
    grid-column-gap: 70px;
    margin-bottom: 45px; }

.gift-img {
    width: auto;
    max-height: 70px;
    margin-bottom: 10px; }

.gift-desc {
    font-size: 18px;
    line-height: 140%;
    span {
        font-weight: 700;
        color: #84CD3A; } }

.header-button {
    border: none;
    width: 400px;
    height: 96px;
    background: url('../img/gifts/button-bg.png');
    background-repeat: no-repeat;
    font-weight: 700;
    font-size: 16px;
    line-height: 100%;
    color: #FFFFFF;
    position: relative;
    span {
        font-weight: 500;
        margin-top: 5px;
        font-size: 14px;
        opacity: .5; }
    &:before {
        content: '';
        position: absolute;
        bottom: -10px;
        height: 40px;
        width: 348px;
        left: 50%;
        transform: translateX(-50%);
        background: #6A9E35;
        opacity: .3;
        filter: blur(20px);
        transition: .3s; }
    &:hover, &:focus, &:active {
        &:before {
            opacity: .5; } } }

.header-img-wrap {
    position: relative;
    padding-bottom: 270%; }

.fir {
    position: absolute;
    bottom: 20px;
    left: -320px; }

.new-line {
    display: inline-block;
    width: 100%; }

.why {
    position: relative; }

.section-title {
    font-weight: 300;
    font-size: 44px;
    line-height: 140%;
    text-align: center;
    margin-bottom: 55px;
    span {
        font-weight: 700;
        color: #84CD3A; } }

.why-title {
    position: relative;
    top: -100px;
    span {
        font-size: 56px;
        line-height: 140%; } }

.why-wrap {
    position: relative;
    text-align: center;
    top: -100px;
    margin-bottom: -70px; }

.tooltips {
    text-align: left; }

.tooltip {
    position: absolute;
    font-weight: 300;
    font-size: 18px;
    line-height: 140%;
    background: #FFFFFF;
    box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.03);
    border-radius: 10px;
    width: 300px;
    min-height: 150px;
    padding: 25px 30px 25px 45px;
    span {
        font-weight: 700;
        color: #84CD3A; } }

.tp-before {
    position: absolute;
    width: 70px;
    height: 70px;
    background: #E7FFCE;
    border-radius: 50%;
    left: 0;
    top: 50%;
    transform: translate(-50%, -50%);
    cursor: pointer;
    &:before {
        content: '+';
        position: absolute;
        font-size: 32px;
        color: #FFFFFF;
        width: 50px;
        height: 50px;
        background: #84CD3A;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%); } }

.tooltip:nth-child(n+4) {
    padding-left: 30px;
    padding-right: 45px;
    .tp-before {
        right: 0;
        left: auto;
        top: 50%;
        transform: translate(50%, -50%); } }

.tooltip:nth-child(1) {
    top: 95px;
    right: 135px; }

.tooltip:nth-child(2) {
    top: 325px;
    right: 70px; }

.tooltip:nth-child(3) {
    top: 555px;
    right: 0px; }

.tooltip:nth-child(4) {
    top: 95px;
    left: 135px; }

.tooltip:nth-child(5) {
    top: 325px;
    left: 70px; }

.tooltip:nth-child(6) {
    top: 555px;
    left: 0px; }

.why-left-fir {
    position: absolute;
    left: 0;
    top: calc( 100% - 780px * 1.5 );
    pointer-events: none; }

.why-right-fir {
    position: absolute;
    right: 0;
    top: calc( 100% - 700px * 1.5 );
    pointer-events: none; }

.section-title .section-sub-title {
    font-weight: 300;
    font-size: 26px;
    line-height: 140%;
    color: rgba(40, 40, 40, .5); }

.why-container {
    position: relative;
    z-index: 10; }

.catalog-container {
    position: relative;
    z-index: 2; }

.catalog {
    position: relative;
    margin-bottom: 100px;
    overflow: visible; }

.catalog-list {
    margin-top: 105px;
    display: grid;
    grid-template: auto / 1fr 1fr 1fr;
    grid-column-gap: 80px;
    grid-row-gap: 100px; }

.catalog-item-wrap {
    position: relative;
    background: #FFFFFF;
    box-shadow: 0px 4px 25px rgba(0, 0, 0, .03);
    border-radius: 15px;
    min-height: 680px;
    &:before, &:after {
        content: '';
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        background: #FFFFFF;
        box-shadow: 0px 4px 25px rgba(0, 0, 0, .03);
        border-radius: 15px; }
    &:before {
        bottom: 20px;
        height: 100%;
        width: calc( 100% + 30px );
        z-index: 1; }
    &:after {
        bottom: 37px;
        height: 100%;
        width: calc( 100% + 50px );
        z-index: 2; } }

.catalog-item {
    position: absolute;
    z-index: 3;
    border: 1px solid #F7F7F7;
    border-radius: 15px;
    width: calc( 100% + 30px );
    height: calc( 100% - 20px );
    left: 50%;
    bottom: 47px;
    transform: translateX(-50%);
    padding: 35px 55px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center; }

.catalog-img-wrap {
    margin-bottom: 15px;
    height: 315px;
    width: 100%; }

.catalog-img {
    max-width: 100%;
    max-height: 100%; }

.catalog-specifications {
    margin-bottom: 15px; }

.specification {
    font-size: 16px;
    line-height: 190%;
    font-weight: 300;
    span {
        font-weight: 700; } }

.catalog-price {
    margin-bottom: 15px;
    font-weight: 300;
    font-size: 22px;
    line-height: 170%;
    color: #84CD3A;
    span {
        font-size: 32px;
        font-weight: 700; } }

.buy-button {
    background: linear-gradient(180deg, #94ED3B 0%, #84CD3A 100%);
    border-radius: 7px;
    font-weight: 600;
    font-size: 14px;
    line-height: 140%;
    color: #FFFFFF;
    text-shadow: 0px 1px 1px rgba(0, 0, 0, .15);
    max-width: 100%;
    width: 240px;
    height: 66px;
    border: 0;
    text-transform: uppercase; }

.catalog-left-cone {
    position: absolute;
    left: 0;
    top: 500px;
    pointer-events: none; }

.catalog-right-cone {
    right: 0;
    position: absolute;
    bottom: 170px;
    pointer-events: none;
    z-index: 1;
    &:nth-of-type(3) {
        bottom: 370px;
        z-index: 0; } }

.bonus-catalog {
    position: relative; }

.bonus-catalog-left-cone {
    position: absolute;
    bottom: -10px;
    left: 0;
    pointer-events: none;
    &:nth-of-type(2) {
        left: 90px;
        bottom: -60px; } }
